<div class="main"><breadcrumb :breadcrumb="breadcrumb"></breadcrumb><div class="div">这是列表页，主要用来显示面包屑</div><div class="div pointer" @click="goInfo">点击进入详情页</div><button @click="showQuestion=true">显示问题弹窗</button><question :visible="showQuestion" @ok="handleQuestionOk" @cancel="showQuestion=false"></question><div>{{questionData}}</div><a-button @click="initWs(1)">创建ws</a-button><a-button @click="sendData">点击发送ws信息xxxx</a-button><a-button @click="initWs(2)">创建与第一个ws相同的连接</a-button><a-button @click="initWs(3)">创建不同的ws连接</a-button><a-button @click="initWs(33)">关闭第二个ws</a-button></div>